<template>
  <div id="mountNode"></div>
</template>

<script>
import { mapState } from "vuex";
import G6 from "@antv/g6";
var graph = null;
export default {
  computed: {
    ...mapState({
      data(state) {
        let summary = state.summary;
        // ÁÇπÈõÜ
        let nodes = [];
        // ËæπÈõÜ
        let edges = [];
        this.addServer(summary, nodes, edges);
        return {
          nodes,
          edges
        };
      }
    })
  },
  methods: {
    addServer(node, nodes, edges) {
      let result = {
        id: node.Address,
        label: node.Address,
        description: `cpu:${node.CPUUsage >> 0}% mem:${node.Memory.Usage >>
          0}%`,
        shape: "modelRect",
        logoIcon: {
          show: false
        }
      };
      nodes.push(result);
      if (node.Rooms) {
        for (let i = 0; i < node.Rooms.length; i++) {
          let room = node.Rooms[i];
          let roomId = result.id + room.StreamPath;
          nodes.push({
            id: roomId,
            label: room.StreamPath,
            shape: "rect"
          });
          edges.push({ source: result.id, target: roomId });
          if (room.SubscriberInfo) {
            for (let j = 0; j < room.SubscriberInfo.length; j++) {
              let subId = roomId + room.SubscriberInfo[j].ID;
              nodes.push({
                id: subId,
                label: room.SubscriberInfo[j].ID
              });
              edges.push({ source: roomId, target: subId });
            }
          }
        }
      }
      if (node.Children) {
        for (let childId in node.Children) {
          this.addServer(node.Children[childId], nodes, edges);
          edges.push({
            source: result.id,
            target: childId
          });
        }
      }
    }
  },
  watch: {
    data(v) {
      if (graph) {
        graph.read(v); // Âä†ËΩΩÊï∞ÊçÆ
      }
    }
  },
  mounted() {
    graph = new G6.Graph({
      renderer: "svg",
      container: "mountNode", // ÊåáÂÆöÊåÇËΩΩÂÆπÂô®
      width: 800, // ÂõæÁöÑÂÆΩÂ∫¶
      height: 500, // ÂõæÁöÑÈ´òÂ∫¶
      layout: {
        type: "radial"
      },
      defaultNode: {}
    });
    graph.read(this.data); // Âä†ËΩΩÊï∞ÊçÆ
  }
};
</script>

